@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;900&family=Open+Sans:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  width:100%;
  max-width:1400px;
  margin:auto;  
  height:auto;
  background-color: white;
  height:60px;
  text-align: left;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
  padding-bottom: 20px;
}
.App-header h1{
  display: inline-block;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 30px;
}
.App-header p{
  display: inline-block;
  color:rgb(141, 141, 141);
  font-family: 'Montserrat',sans-serif;
  font-weight: 400;
  font-size:16px;
  margin-left:20px;
}
.App-section{
background-color: #f5f5f5;

}
.App-section .container{
  width:100%;
  
  max-width:1400px;
  margin:auto;  
  height:auto;
  padding-top:150px;
  padding-bottom:150px;
  text-align: left;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.App-section h3{
  font-family: 'Open Sans',sans-serif;
  color:#e45776;
  font-size:26px;
  margin-bottom:-20px;
}
.App-section p{
  font-family: 'Open Sans',sans-serif;
  color:gray;
  max-width: 500px;
  width:90%;
}
.button{
  margin-top:10px;
  padding:10px;
  padding-right:50px;
  padding-left:50px;
  background-color: #e45776;
  border-radius: 30px;
  width:auto;
  border:3px solid #e45776;
  display: inline-block;
  text-align: center;
  color:white;
}
.button:hover{
  cursor: pointer;
  background-color: #f5f5f5;
  
  color:#e45776;
}
.button:hover h3{
  color:#e45776;
}
.button h3{
  margin:0;
  color:white;
}
.App-section h1{
  font-family: 'Montserrat',sans-serif;
  font-weight: 900;
  font-size:60px;
  letter-spacing: 0px;
  line-height: 50px;
}
.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

